import { Button, Modal } from 'antd'
import { useState } from 'react'


export const Header = () => {

  const [introShow, setIntroShow] = useState(true)

  const exportPDF = () => {
    window.print()
  }


  return (
    <div className="opcv-header">
      <div className="left-area">
        <div className="home">OnePageCV</div>
      </div>
      <div className="right-area">
        <Button type="primary" className="export" onClick={() => setIntroShow(true)} danger></Button>
        <Button type="primary" className="export" onClick={exportPDF}></Button>
      </div>
      <Modal
        title=''
        open={introShow}
        onCancel={() => setIntroShow(false)}
        footer={[<Button type='primary' onClick={() => setIntroShow(false)}></Button>]}
      >
        <div style={{ color: '#FF4D4F' }}></div>
        <div style={{marginTop: 20}}>
          
          <ul>
            <li>[]</li>
            <li>[]</li>
            <li></li>
            <li>
              []
            </li>
            <li>[]</li>
            <li>A4</li>
          </ul>
        </div>
        <div style={{marginTop: 20}}>
          
          <ul>
            <li>[][]</li>
            <li>ctrl+b()ctrl+i()</li>
            <li>htmlabu</li>
          </ul>
        </div>
        <div style={{marginTop: 20}}>[]</div>
        <div style={{marginTop: 20}}>1556330234@qq.com</div>
      </Modal>
    </div>
  )
}